import React, { useState, useEffect, useRef, useCallback } from 'react';
import { db } from './firebase';
import { 
    collection, 
    doc, 
    setDoc, 
    getDocs,
    query,
    getDoc
} from 'firebase/firestore';
import Papa from 'papaparse';

function PokerLedger() {
    const [url, setUrl] = useState('');
    const [ledgerData, setLedgerData] = useState(null);
    const [error, setError] = useState(null);
    const [selectedPlayer, setSelectedPlayer] = useState('');
    const [venmoId, setVenmoId] = useState('');
    const [saveMessage, setSaveMessage] = useState(null);
    const [playerVenmoMap, setPlayerVenmoMap] = useState({});
    const [isLoading, setIsLoading] = useState(false);
    const fileInputRef = useRef(null);

    // Create a unique ID for this instance
    const inputId = 'poker-ledger-file-input';

    // Load Venmo IDs from Firebase on component mount
    useEffect(() => {
        const loadVenmoIds = async () => {
            console.log('Starting to load Venmo IDs...');
            try {
                if (!db) {
                    console.error('Firebase DB not initialized');
                    setSaveMessage({ 
                        type: 'error', 
                        text: 'Database connection error' 
                    });
                    return;
                }

                // Add collection existence check
                const venmoCollection = collection(db, 'venmoIds');
                const q = query(venmoCollection);
                
                console.log('Attempting to fetch documents...');
                const querySnapshot = await getDocs(q);
                
                if (querySnapshot.empty) {
                    console.log('No documents found in venmoIds collection');
                    return;
                }
                
                const venmoData = {};
                querySnapshot.forEach((doc) => {
                    console.log('Document found:', doc.id);
                    const data = doc.data();
                    if (data.playerName && data.venmoId) {
                        venmoData[data.playerName] = data.venmoId;
                    }
                });
                
                console.log('Loaded Venmo data:', venmoData);
                setPlayerVenmoMap(venmoData);
            } catch (error) {
                console.error('Error loading Venmo IDs:', {
                    message: error.message,
                    code: error.code,
                    stack: error.stack,
                    name: error.name
                });
                setSaveMessage({ 
                    type: 'error', 
                    text: `Error loading saved Venmo IDs: ${error.message}` 
                });
            }
        };

        loadVenmoIds();
    }, []);

    const handleSubmit = async (e) => {
        e.preventDefault();
        setIsLoading(true);
        try {
            const gameId = url.split('/games/')[1]?.split('/')[0];
            if (!gameId) {
                throw new Error('Invalid URL format. Please provide a valid Poker Now game URL.');
            }

            let gameDoc = null;

            try {
                const gameRef = doc(db, 'games', gameId);
                gameDoc = await getDoc(gameRef);
                
                if (gameDoc.exists()) {
                    console.log('Game found in database');
                    const gameData = gameDoc.data();
                    
                    // Calculate time differences
                    const lastUpdateTime = new Date(gameData.updatedAt).getTime();
                    const createdTime = new Date(gameData.createdAt).getTime();
                    const fiveMinutesAgo = Date.now() - (5 * 60 * 1000);
                    const timeSinceCreation = lastUpdateTime - createdTime;
                    const twentyFourHours = 24 * 60 * 60 * 1000;

                    // Show stored data immediately
                    setLedgerData({
                        playersInfos: gameData.playersInfos,
                        updatedAt: gameData.updatedAt
                    });
                    setError(null);
                    
                    // Only fetch new data if within 24 hours and older than 5 minutes
                    if (timeSinceCreation <= twentyFourHours && lastUpdateTime < fiveMinutesAgo) {
                        console.log('Data is older than 5 minutes and game is within 24 hours of creation, fetching fresh data in background...');
                        
                        // Remove loading state since we're showing stored data
                        setIsLoading(false);
                        
                        // Fetch new data in background
                        const proxyUrl = 'https://poker-proxy.tlproxy.workers.dev';
                        const pokerNowUrl = `https://www.pokernow.club/games/${gameId}/players_sessions`;
                        
                        fetch(proxyUrl, {
                            method: 'POST',
                            headers: {
                                'Content-Type': 'application/json',
                            },
                            body: JSON.stringify({
                                url: pokerNowUrl
                            })
                        })
                        .then(response => {
                            if (!response.ok) throw new Error(`HTTP error! status: ${response.status}`);
                            return response.json();
                        })
                        .then(async data => {
                            const now = new Date().toISOString();
                            
                            // Update Firebase
                            await setDoc(doc(db, 'games', gameId), {
                                gameId: gameId,
                                playersInfos: data.playersInfos,
                                updatedAt: now,
                                createdAt: gameDoc.data().createdAt,
                                settlements: calculateSettlements(data.playersInfos)
                            });
                            
                            // Update UI with new data
                            setLedgerData({
                                ...data,
                                updatedAt: now
                            });
                            console.log('Background refresh complete');
                        })
                        .catch(error => {
                            console.error('Background refresh failed:', error);
                            // Don't show error to user since they still have working stored data
                        });
                        
                        return;
                    } else {
                        console.log('Using stored data without refresh...');
                        setIsLoading(false);
                        return;
                    }
                }
            } catch (dbError) {
                console.error('Error checking database:', dbError);
                // Continue to fetch from PokerNow if database check fails
            }

            // If no stored data exists, fetch new data normally
            console.log('No stored data found, fetching from PokerNow...');
            const proxyUrl = 'https://poker-proxy.tlproxy.workers.dev';
            const pokerNowUrl = `https://www.pokernow.club/games/${gameId}/players_sessions`;
            
            const response = await fetch(proxyUrl, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({
                    url: pokerNowUrl
                })
            });

            if (!response.ok) {
                throw new Error(`HTTP error! status: ${response.status}`);
            }

            const data = await response.json();
            const now = new Date().toISOString();
            
            try {
                await setDoc(doc(db, 'games', gameId), {
                    gameId: gameId,
                    playersInfos: data.playersInfos,
                    updatedAt: now,
                    createdAt: now,
                    settlements: calculateSettlements(data.playersInfos)
                });
                console.log('Game data saved successfully');
            } catch (dbError) {
                console.error('Error saving game data:', dbError);
            }

            setLedgerData({
                ...data,
                updatedAt: now
            });
            setError(null);
        } catch (error) {
            console.error('Error fetching data:', error);
            setError(`Error fetching data: ${error.message}`);
            setLedgerData(null);
        } finally {
            setIsLoading(false);
        }
    };

    // Add new refresh handler
    const handleRefresh = async (e) => {
        e.preventDefault();
        setIsLoading(true);
        try {
            const gameId = url.split('/games/')[1]?.split('/')[0];
            if (!gameId) {
                throw new Error('Invalid URL format. Please provide a valid Poker Now game URL.');
            }

            console.log('Fetching fresh data...');
            console.log('Making request to:', 'https://poker-proxy.tlproxy.workers.dev');
            
            try {
                const response = await fetch('https://poker-proxy.tlproxy.workers.dev', {
                    method: 'POST',
                    mode: 'cors',
                    credentials: 'omit',
                    headers: {
                        'Content-Type': 'application/json',
                        'Accept': 'application/json',
                    },
                    body: JSON.stringify({
                        url: url
                    })
                });

                console.log('Response received:', response);
                
                if (!response.ok) {
                    const errorText = await response.text();
                    console.error('Error response:', errorText);
                    throw new Error(`HTTP error! status: ${response.status}. Response: ${errorText}`);
                }

                const data = await response.json();
                console.log('Data received:', data);

                const now = new Date().toISOString();

                // Get the existing document to preserve createdAt
                const gameRef = doc(db, 'games', gameId);
                const gameDoc = await getDoc(gameRef);
                
                await setDoc(gameRef, {
                    gameId: gameId,
                    playersInfos: data.playersInfos,
                    updatedAt: now,
                    createdAt: gameDoc.exists() ? gameDoc.data().createdAt : now,
                    settlements: calculateSettlements(data.playersInfos)
                });
                
                console.log('Game data refreshed successfully');

                setLedgerData({
                    ...data,
                    updatedAt: now
                });
                setError(null);
            } catch (fetchError) {
                console.error('Fetch error details:', {
                    message: fetchError.message,
                    stack: fetchError.stack,
                    type: fetchError.type,
                    name: fetchError.name
                });
                throw new Error(`Network error: ${fetchError.message}`);
            }
        } catch (error) {
            console.error('Error refreshing data:', error);
            setError(`Error refreshing data: ${error.message}`);
            setLedgerData(null);
        } finally {
            setIsLoading(false);
        }
    };

    const formatAmount = (amount) => {
        return new Intl.NumberFormat('en-US', {
            style: 'currency',
            currency: 'USD',
            minimumFractionDigits: 2,
            maximumFractionDigits: 2,
        }).format(amount / 100);
    };

    const calculateSettlements = (playersInfos) => {
        let debtors = [];
        let creditors = [];

        const players = Array.isArray(playersInfos) ? playersInfos : Object.values(playersInfos);

        // First pass to identify debtors and creditors
        players.forEach(player => {
            const net = player.net ?? (player.stack - player.totalBuyIn);
            
            if (!player.name) {
                console.warn('Player missing name:', player);
                return;
            }

            const roundedNet = Math.round(net);
            
            if (roundedNet < 0) {
                debtors.push({ id: player.id, name: player.name, amount: -roundedNet });
            } else if (roundedNet > 0) {
                creditors.push({ id: player.id, name: player.name, amount: roundedNet });
            }
        });

        // Sort by amount (largest first)
        debtors.sort((a, b) => b.amount - a.amount);
        creditors.sort((a, b) => b.amount - a.amount);

        let settlements = [];
        let totalToSettle = debtors.reduce((sum, d) => sum + d.amount, 0);
        let settledAmount = 0;

        // Keep track of remaining amounts for each creditor
        let remainingCredits = new Map(creditors.map(c => [c.name, c.amount]));

        // Process each debtor
        debtors.forEach(debtor => {
            let remainingDebt = debtor.amount;
            
            // Try to settle with each creditor
            for (const creditor of creditors) {
                if (remainingDebt <= 0) break;
                
                const availableCredit = remainingCredits.get(creditor.name);
                if (availableCredit <= 0) continue;

                const amount = Math.min(remainingDebt, availableCredit);
                
                if (amount > 0) {
                    settlements.push({
                        from: debtor.name,
                        to: creditor.name,
                        amount: amount
                    });

                    remainingDebt -= amount;
                    remainingCredits.set(creditor.name, availableCredit - amount);
                    settledAmount += amount;
                }
            }

            if (remainingDebt > 0) {
                console.warn(`Unable to fully settle ${debtor.name}'s remaining debt: ${remainingDebt}`);
            }
        });

        // Verification
        console.log('Settlement verification:', {
            totalToSettle,
            settledAmount,
            difference: totalToSettle - settledAmount
        });

        return settlements;
    };

    const handlePlayerSelect = (e) => {
        const playerId = e.target.value;
        setSelectedPlayer(playerId);
        
        // Get player name from ledgerData
        if (playerId && ledgerData && ledgerData.playersInfos) {
            // Find the player in the array
            const player = Array.isArray(ledgerData.playersInfos) 
                ? ledgerData.playersInfos.find(p => p.id === playerId)
                : ledgerData.playersInfos[playerId];

            if (player) {
                const playerName = player.name;
                if (playerName) {
                    // Check if we have a Venmo ID for this player
                    setVenmoId(playerVenmoMap[playerName] ? `@${playerVenmoMap[playerName]}` : '');
                } else {
                    setVenmoId('');
                }
            } else {
                setVenmoId('');
            }
        } else {
            setVenmoId('');
        }
    };

    const handleVenmoIdChange = (e) => {
        setVenmoId(e.target.value);
    };

    const handleSaveSettings = async () => {
        if (!selectedPlayer) {
            setSaveMessage({ type: 'error', text: 'Please select a player first' });
            return;
        }
        if (!venmoId) {
            setSaveMessage({ type: 'error', text: 'Please enter your Venmo ID' });
            return;
        }

        // Additional validation
        const venmoIdRegex = /^@?[\w.-]+$/;
        if (!venmoIdRegex.test(venmoId)) {
            setSaveMessage({ type: 'error', text: 'Invalid Venmo ID format' });
            return;
        }

        try {
            // Find the player in the playersInfos array
            const player = ledgerData.playersInfos.find(p => p.id === selectedPlayer);
            if (!player) {
                throw new Error('Selected player not found');
            }

            const playerName = player.name;
            if (!playerName) {
                throw new Error('Player name not found');
            }

            // Log the data we're trying to save
            console.log('Attempting to save with:', {
                selectedPlayer,
                playerName,
                venmoId,
                db: !!db
            });

            if (!db) {
                throw new Error('Firebase database not initialized');
            }

            const cleanVenmoId = venmoId.replace('@', '');

            // Save to Firebase
            await setDoc(doc(db, 'venmoIds', selectedPlayer), {
                playerName: playerName,
                pokerNowId: selectedPlayer,
                venmoId: cleanVenmoId,
                updatedAt: new Date().toISOString()
            });

            // Update local state
            setPlayerVenmoMap(prev => ({
                ...prev,
                [playerName]: cleanVenmoId
            }));

            setSaveMessage({ type: 'success', text: 'Settings saved successfully!' });
            setTimeout(() => {
                setSaveMessage(null);
            }, 3000);
        } catch (error) {
            // More detailed error logging
            console.error('Error saving Venmo ID:', {
                message: error.message,
                code: error.code,
                stack: error.stack,
                name: error.name
            });
            
            setSaveMessage({ 
                type: 'error', 
                text: `Error saving settings: ${error.message}` 
            });
        }
    };

    const getSortedPlayers = (ledgerData) => {
        if (!ledgerData?.playersInfos) return [];
        return [...ledgerData.playersInfos].sort((a, b) => (b.stack || 0) - (a.stack || 0));
    };

    const getSortedResults = (players) => {
        if (!players || !Array.isArray(players)) return [];
        return players.map(player => ({
            name: player.name || '',
            stack: player.stack || 0,
            totalBuyIn: player.totalBuyIn || 0,
            net: (player.stack || 0) - (player.totalBuyIn || 0)
        })).sort((a, b) => b.net - a.net);
    };

    const getPlayerNameById = (playersInfos, id) => {
        return playersInfos[id]?.names[0] || '';
    };

    const handleSettleUp = (settlement, isRequest = false) => {
        // Get the recipient's Venmo ID from the mapping
        const recipientVenmoId = playerVenmoMap[isRequest ? settlement.from : settlement.to];
        
        if (!recipientVenmoId) {
            alert(`No Venmo ID saved for ${isRequest ? settlement.from : settlement.to}. Please ask them to set up their Venmo ID.`);
            return;
        }

        // Convert amount from cents to dollars
        const cleanAmount = (settlement.amount / 100).toFixed(2);
        
        // Construct the Venmo URL with appropriate transaction type
        const venmoUrl = `https://venmo.com/${recipientVenmoId}?txn=${isRequest ? 'request' : 'pay'}&note=TL%20Online&amount=${cleanAmount}`;
        
        // Open in new tab
        window.open(venmoUrl, '_blank');
    };

    const formatDateTime = (isoString) => {
        const date = new Date(isoString);
        return date.toLocaleString('en-US', {
            month: 'short',
            day: 'numeric',
            year: 'numeric',
            hour: 'numeric',
            minute: '2-digit',
            hour12: true
        });
    };

    const styles = {
        tableContainer: {
            width: '100%',
            marginBottom: '20px',
            overflowX: 'auto',
            msOverflowStyle: 'none',  // Hide scrollbar in IE/Edge
            scrollbarWidth: 'none',   // Hide scrollbar in Firefox
            '&::-webkit-scrollbar': { // Hide scrollbar in Chrome/Safari
                display: 'none'
            }
        },
        table: {
            width: '100%',
            minWidth: '600px',
            borderCollapse: 'collapse',
            tableLayout: 'fixed', // Force table to respect column widths
        },
        tableCell: {
            padding: '16px 8px',
            whiteSpace: 'nowrap',
            overflow: 'hidden',
            textOverflow: 'ellipsis',
            width: '25%', // Set consistent width
        },
        arrowColumn: {
            textAlign: 'center',
            width: '40px',
            padding: '16px 4px',
            verticalAlign: 'middle',
            '@media screen and (max-width: 768px)': {
                width: '20px',
                padding: '16px 2px'
            }
        },
        arrowHeader: {
            textAlign: 'center',
            width: '40px',
            padding: '16px 4px',
            verticalAlign: 'middle',
            '@media screen and (max-width: 768px)': {
                width: '20px',
                padding: '16px 2px'
            }
        },
        amountColumn: {
            width: '100px',
            '@media screen and (max-width: 768px)': {
                width: '70px'
            }
        },
        tableRow: {
            cursor: 'pointer',
            transition: 'background-color 0.2s',
            position: 'relative',
        },
        highlightedRow: {
            backgroundColor: 'rgba(33, 150, 243, 0.1)',
            borderLeft: '4px solid rgb(33, 150, 243)',
            '& td:first-child': {
                paddingLeft: '12px'
            }
        },
        settleButtonColumn: {
            width: '100px',
            '@media screen and (max-width: 768px)': {
                width: '80px'
            }
        },
        buttonContainer: {
            display: 'flex',
            gap: '10px',
            marginTop: '10px'
        },
        primaryButton: {
            padding: '8px 16px',
            borderRadius: '4px',
            border: 'none',
            backgroundColor: '#2a2a2a',
            color: 'white',
            cursor: 'pointer',
            '&:hover': {
                backgroundColor: '#1976d2'
            }
        },
        refreshButton: {
            padding: '8px 16px',
            borderRadius: '4px',
            border: 'none',
            backgroundColor: '#2a2a2a',
            color: 'white',
            cursor: 'pointer',
            transition: 'background-color 0.2s',
            '&:hover': {
                backgroundColor: '#1976d2'
            }
        },
        cardHeader: {
            backgroundColor: '#2a2a2a',
            padding: '15px',
            borderRadius: '4px',
            margin: '0 0 15px 0',
            fontSize: '1.2em',
            fontWeight: '500',
            color: 'white',
            display: 'flex',
            alignItems: 'baseline',
            gap: '10px',
            textAlign: 'left',
            width: '100%'
        },
        spinner: {
            display: 'inline-block',
            width: '16px',
            height: '16px',
            border: '2px solid rgba(255, 255, 255, 0.3)',
            borderRadius: '50%',
            borderTopColor: 'white',
            animation: 'spin 1s linear infinite',
            marginLeft: '8px'
        }
    };

    // Update the media styles
    const mediaStyles = document.createElement('style');
    mediaStyles.textContent = `
        @media screen and (max-width: 768px) {
            .ledger-table {
                width: 100%;
            }
            
            .ledger-table > div {
                overflow-x: auto;
                -webkit-overflow-scrolling: touch;
                scrollbar-width: none;
                -ms-overflow-style: none;
            }
            
            .ledger-table > div::-webkit-scrollbar {
                display: none;
            }
            
            table {
                min-width: 600px;
            }
            
            td:last-child,
            th:last-child {
                width: 80px;
            }
            
            table tr {
                transition: background-color 0.2s;
            }
            
            table tr:hover {
                background-color: #f5f5f5;
            }
            
            table tr:active {
                background-color: #e0e0e0;
            }
            
            td, th {
                vertical-align: middle;
            }
            
            .selected-player {
                background-color: rgba(33, 150, 243, 0.1) !important;
                border-left: 4px solid rgb(33, 150, 243) !important;
            }
            
            .selected-player td:first-child {
                padding-left: 12px !important;
            }
        }
        @keyframes spin {
            to {
                transform: rotate(360deg);
            }
        }
    `;
    document.head.appendChild(mediaStyles);

    const processFile = useCallback((file) => {
        if (!file || !(file instanceof File)) {
            console.log('Invalid file object:', file);
            return;
        }

        // Extract game ID from filename
        const gameId = file.name.startsWith('ledger_') 
            ? file.name.replace('ledger_', '').split('.')[0]
            : null;

        if (!gameId) {
            console.warn('Could not extract game ID from filename:', file.name);
        }

        setIsLoading(true);
        setError(null);

        Papa.parse(file, {
            header: true,
            skipEmptyLines: true,
            complete: async (results) => {
                try {
                    if (!results?.data || !Array.isArray(results.data)) {
                        throw new Error('Invalid CSV format');
                    }

                    // First, group all transactions by player ID
                    const playerTransactions = {};
                    
                    // Process each row
                    for (const row of results.data) {
                        if (!row?.player_id || !row?.player_nickname) continue;
                        
                        const id = row.player_id;
                        const name = row.player_nickname.trim();
                        
                        // Initialize player if not exists
                        if (!playerTransactions[id]) {
                            playerTransactions[id] = {
                                id,
                                name,
                                buyIns: [],          // Track all buy-ins
                                cashOuts: [],        // Track all cash-outs
                                stacks: []           // Track all stacks
                            };
                        }
                        
                        // Add buy-in amount
                        const buyIn = parseInt(row.buy_in) || 0;
                        if (buyIn > 0) {
                            playerTransactions[id].buyIns.push(buyIn);
                        }
                        
                        // Add cash-out amount
                        const cashOut = parseInt(row.buy_out) || 0;
                        if (cashOut > 0) {
                            playerTransactions[id].cashOuts.push(cashOut);
                        }
                        
                        // Add stack amount
                        const stack = parseInt(row.stack) || 0;
                        if (stack > 0) {
                            playerTransactions[id].stacks.push(stack);
                        }
                    }

                    // Calculate final player summaries
                    const playerSummaries = Object.values(playerTransactions).map(player => {
                        // Calculate total buy-in
                        const totalBuyIn = player.buyIns.reduce((sum, amount) => sum + amount, 0);
                        
                        // Calculate total cash out (sum of all cash-outs and stacks)
                        const totalCashOut = player.cashOuts.reduce((sum, amount) => sum + amount, 0) +
                                           player.stacks.reduce((sum, amount) => sum + amount, 0);
                        
                        // Calculate net profit/loss
                        const net = totalCashOut - totalBuyIn;

                        return {
                            id: player.id,
                            name: player.name,
                            totalBuyIn,
                            totalCashOut,
                            net
                        };
                    });

                    console.log('Player Summaries:', playerSummaries);

                    const transformedData = {
                        playersInfos: playerSummaries
                    };

                    // Upload game data to Firebase using gameId from filename
                    if (gameId && db) {
                        const gameRef = doc(db, 'games', gameId);
                        
                        try {
                            // First check if the document exists
                            const docSnap = await getDoc(gameRef);
                            const now = new Date().toISOString();
                            
                            if (docSnap.exists()) {
                                // Update existing record
                                await setDoc(gameRef, {
                                    gameId,
                                    playersInfos: playerSummaries,
                                    updatedAt: now,
                                    createdAt: docSnap.data().createdAt, // Keep original creation date
                                    settlements: calculateSettlements(playerSummaries)
                                });
                                console.log('Game data updated in Firebase with ID:', gameId);
                            } else {
                                // Create new record
                                await setDoc(gameRef, {
                                    gameId,
                                    playersInfos: playerSummaries,
                                    updatedAt: now,
                                    createdAt: now, // New creation date for new records
                                    settlements: calculateSettlements(playerSummaries)
                                });
                                console.log('New game data uploaded to Firebase with ID:', gameId);
                            }
                        } catch (dbError) {
                            console.error('Error saving game data:', dbError);
                        }
                    }

                    // Fetch Venmo IDs for all players
                    try {
                        const venmoCollection = collection(db, 'venmoIds');
                        const querySnapshot = await getDocs(venmoCollection);
                        const venmoData = {};
                        
                        querySnapshot.forEach((doc) => {
                            const data = doc.data();
                            if (data.playerName && data.venmoId) {
                                venmoData[data.playerName] = data.venmoId;
                            }
                        });
                        
                        setPlayerVenmoMap(venmoData);
                    } catch (error) {
                        console.error('Error loading Venmo IDs:', error);
                    }

                    setLedgerData(transformedData);
                    
                } catch (err) {
                    console.error('Processing error:', err);
                    setError(err.message);
                    setLedgerData(null);
                } finally {
                    setIsLoading(false);
                }
            },
            error: (err) => {
                console.error('CSV Parse Error:', err);
                setError('Failed to parse CSV file');
                setLedgerData(null);
                setIsLoading(false);
            }
        });
    }, [db]);

    // Use a button click to trigger file selection
    const handleFileSelect = useCallback(() => {
        // Create a temporary file input
        const input = document.createElement('input');
        input.type = 'file';
        input.accept = '.csv';
        
        // Add the only event listener we need
        input.onchange = (e) => {
            const file = e.target?.files?.[0];
            if (file) {
                processFile(file);
            }
            // Clean up
            document.body.removeChild(input);
        };

        // Hide the input and add it to the body
        input.style.display = 'none';
        document.body.appendChild(input);
        
        // Trigger the file selection
        input.click();
    }, [processFile]);

    const players = getSortedPlayers(ledgerData);
    const results = getSortedResults(players);

    return (
        <div className="poker-ledger">
            <nav className="navbar">
                <div className="navbar-content">
                    <div className="navbar-brand">
                        <svg 
                            className="navbar-logo" 
                            viewBox="0 0 40 40" 
                            xmlns="http://www.w3.org/2000/svg"
                        >
                            {/* Circle background */}
                            <circle 
                                cx="20" 
                                cy="20" 
                                r="18" 
                                fill="none" 
                                stroke="currentColor" 
                                strokeWidth="4"
                            />
                            
                            {/* Letters "TL" with increased weight */}
                            <text
                                x="20"
                                y="25"
                                fontSize="16"
                                fontWeight="900"
                                fill="currentColor"
                                textAnchor="middle"
                                fontFamily="Arial Black, Arial, sans-serif"
                            >
                                TL
                            </text>
                        </svg>
                        <h1>Tiny Leagues Online</h1>
                    </div>
                    {/* Add more nav items here in the future */}
                </div>
            </nav>
            
            <div className="main-content">
                <div className="search-form" style={{ marginTop: '10px' }}>
                    <input
                        type="text"
                        value={url}
                        onChange={(e) => setUrl(e.target.value)}
                        placeholder="Enter your Poker Now game URL"
                        style={{ display: 'none' }}
                    />
                    <div style={{ marginBottom: '20px', textAlign: 'left' }}>
                        <p>To use this app, you must first download the ledger from your pokernow.club game:</p>
                        <ol style={{ marginTop: '10px', marginLeft: '20px' }}>
                            <li>Go to your game, click Log/Ledger (or press L)</li>
                            <li>Click Ledger and then hit the Download Ledger button</li>
                        </ol>
                    </div>
                    <div style={{ ...styles.buttonContainer, marginBottom: '10px' }}>
                        <button
                            type="button"
                            onClick={handleFileSelect}
                            style={{
                                ...styles.primaryButton,
                                display: 'flex',
                                alignItems: 'center',
                                justifyContent: 'center',
                                minWidth: '140px'
                            }}
                            disabled={isLoading}
                        >
                            {isLoading ? (
                                <>
                                    Loading
                                    <div style={styles.spinner} />
                                </>
                            ) : (
                                'Upload CSV'
                            )}
                        </button>
                    </div>
                </div>

                {error && <div className="error"> {error}</div>}
                
                {ledgerData && (
                    <div className="ledger-table">
                        <h2 style={styles.cardHeader}>Player Settings</h2>
                        <div className="settings-content">
                            <div className="settings-group">
                                <label htmlFor="playerSelect">Select your player name:</label>
                                <select 
                                    id="playerSelect"
                                    value={selectedPlayer}
                                    onChange={handlePlayerSelect}
                                >
                                    <option value="">Select player...</option>
                                    {getSortedPlayers(ledgerData)
                                        .sort((a, b) => a.name.localeCompare(b.name))
                                        .map(player => (
                                            <option key={player.id} value={player.id}>
                                                {player.name}
                                            </option>
                                        ))}
                                </select>

                                <label htmlFor="venmoId">Your Venmo ID:</label>
                                <input
                                    id="venmoId"
                                    type="text"
                                    value={venmoId}
                                    onChange={handleVenmoIdChange}
                                    placeholder="@your-venmo-id"
                                />

                                <button onClick={handleSaveSettings} className="save-button">
                                    Save Settings
                                </button>
                            </div>
                            
                            {saveMessage && (
                                <span className={`save-message ${saveMessage.type}`}>
                                    {saveMessage.text}
                                </span>
                            )}
                        </div>
                    </div>
                )}

                {selectedPlayer && ledgerData && (
                    <>
                        <div className="ledger-table">
                            <h2 style={styles.cardHeader}>
                                Session Results
                                {ledgerData.updatedAt && (
                                    <span style={{
                                        fontSize: '0.6em',
                                        color: 'white',
                                        fontWeight: 'normal'
                                    }}>
                                        Last Refreshed: {formatDateTime(ledgerData.updatedAt)}
                                    </span>
                                )}
                            </h2>
                            <div style={styles.tableContainer}>
                                <table style={styles.table}>
                                    <thead>
                                        <tr>
                                            <th>Player</th>
                                            <th>Buy In</th>
                                            <th>Cash Out</th>
                                            <th>Net P/L</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {ledgerData.playersInfos
                                            .sort((a, b) => b.net - a.net) // Sort by net P/L, descending
                                            .map((player) => (
                                                <tr key={player.id}>
                                                    <td>{player.name}</td>
                                                    <td>{formatAmount(player.totalBuyIn)}</td>
                                                    <td>{formatAmount(player.totalCashOut)}</td>
                                                    <td style={{
                                                        color: player.net > 0 ? 'green' : player.net < 0 ? 'red' : 'black',
                                                        fontWeight: 'bold'
                                                    }}>
                                                        {formatAmount(player.net)}
                                                    </td>
                                                </tr>
                                            ))}
                                    </tbody>
                                </table>
                            </div>
                        </div>

                        <div className="ledger-table">
                            <h2 style={styles.cardHeader}>Settlement</h2>
                            <div style={styles.tableContainer}>
                                <table style={styles.table}>
                                    <colgroup>
                                        <col style={{width: '25%'}} />
                                        <col style={{width: '40px'}} />
                                        <col style={{width: '25%'}} />
                                        <col style={{width: '100px'}} />
                                        <col style={{width: '100px'}} />
                                    </colgroup>
                                    <thead>
                                        <tr>
                                            <th style={styles.tableCell}>From</th>
                                            <th style={styles.arrowColumn}></th>
                                            <th style={styles.tableCell}>To</th>
                                            <th style={{...styles.tableCell, ...styles.amountColumn}}>Amount</th>
                                            <th style={styles.settleButtonColumn}>Settle Up</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {calculateSettlements(ledgerData.playersInfos).map((settlement, index) => {
                                            // Find the selected player's name
                                            const selectedPlayerData = ledgerData.playersInfos.find(p => p.id === selectedPlayer);
                                            const selectedPlayerName = selectedPlayerData?.name;
                                            
                                            // Check if this settlement involves the selected player (either paying or receiving)
                                            const isSelectedPlayerPaying = selectedPlayerName === settlement.from;
                                            const isSelectedPlayerReceiving = selectedPlayerName === settlement.to;
                                            const isSelectedPlayerInvolved = isSelectedPlayerPaying || isSelectedPlayerReceiving;
                                            
                                            return (
                                                <tr 
                                                    key={index}
                                                    style={{
                                                        ...styles.tableRow,
                                                        ...(isSelectedPlayerInvolved ? styles.highlightedRow : {}),
                                                        cursor: isSelectedPlayerInvolved ? 'pointer' : 'default'
                                                    }}
                                                    onClick={() => {
                                                        if (window.innerWidth <= 768 && isSelectedPlayerInvolved) {
                                                            handleSettleUp(settlement, isSelectedPlayerReceiving);
                                                        }
                                                    }}
                                                    onTouchStart={(e) => {
                                                        if (isSelectedPlayerInvolved) {
                                                            e.currentTarget.style.backgroundColor = '#e0e0e0';
                                                        }
                                                    }}
                                                    onTouchEnd={(e) => {
                                                        if (isSelectedPlayerInvolved) {
                                                            e.currentTarget.style.backgroundColor = 'rgba(33, 150, 243, 0.1)';
                                                        }
                                                    }}
                                                >
                                                    <td>{settlement.from}</td>
                                                    <td style={styles.arrowColumn}></td>
                                                    <td>{settlement.to}</td>
                                                    <td>{formatAmount(Math.abs(settlement.amount))}</td>
                                                    <td style={styles.settleButtonColumn}>
                                                        {isSelectedPlayerInvolved && (
                                                            <button 
                                                                onClick={(e) => {
                                                                    e.stopPropagation();
                                                                    handleSettleUp(settlement, isSelectedPlayerReceiving);
                                                                }}
                                                            >
                                                                {isSelectedPlayerReceiving ? 'Request' : 'Pay'}
                                                            </button>
                                                        )}
                                                    </td>
                                                </tr>
                                            );
                                        })}
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </>
                )}

                {!selectedPlayer && ledgerData && (
                    <div className="select-player-message">
                        Please select your player name above to view session results and settlements.
                    </div>
                )}
            </div>
        </div>
    );
}

export default PokerLedger;
